.background {
    background: url("asset/bg.png") no-repeat;
    position: relative;
    width: 100vw;
    height: 100vh;
    opacity: 1;
    background-size: 100% 100%;
}

.bg-mobile {
    background: url("asset/m_bg.jpg") no-repeat;
    position: relative;
    width: 100vw;
    height: 100vh;
    opacity: 1;
    background-size: 100% 100%;
}

.foreGround {
    background: url("asset/fg.png") no-repeat;
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 100%;
    z-index: 3;
    background-size: 100% 100%;
    pointer-events: none;

}

.btn-group {
    position: absolute;
    bottom: 16%;
    width: 100%;
    height: 60%;
    display: grid;
    grid-template-columns: 72fr 86fr 100fr 86fr 72fr;
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-items: center;
    align-items: flex-end;

}

.card {
    width: 100%;
    transform: scale(1.2, 1.2);
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: relative;
}

.img-card {
    width: 100%;
    position: relative;
}

.content {
    position: absolute;
    top: 0px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo-link {
    width: 40%;
    margin-top: 20%;
}

.logo-link-ins {
    width: 50%;
    margin-top: 20%;
    margin-left: 20%;

}

.about {
    position: fixed;
    right: 0px;
    top: 0px;
    width: 20%;
    content: url(asset/about.png);
    cursor: pointer;
}

.flyBird {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 77%;
    content: url(asset/fly.png);
}

.teamMember {
    width: 100%;
    height: 100%;
    content: url(asset/team.jpg);
    z-index: 5;
    position: absolute;
}

.img-logo {
    width: 100%;
}

.m-button {
    width: 60%;
    max-width: 600px;
    border-color: white;
    border-style: solid;
    border-width: 1px;
    text-decoration: none;
    border-radius: 5px;
    font-weight: bolder;
}

.discard-card {
    background: url(asset/card.png);
}